<template>
  <v-container fluid fill-height>
    <v-layout row fill-height>
      <v-flex
        md6
        offset-md3
      >
        <v-card>
          <v-toolbar
            dense
            flat
          >
            <v-toolbar-title>
              Input Data User
            </v-toolbar-title>
          </v-toolbar>
          <v-card-text>
            <v-text-field
              label="Nama"
              v-model="user.name"
            >
            </v-text-field>
            <v-text-field
              label="Username"
              v-model="user.username"
            >
            </v-text-field>
            <v-text-field
              label="Password"
              v-model="user.password"
              type="password"
            >
            </v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-btn
              dark
              flat
              color="blue"
              @click="updateUser"
            >
              Simpan
            </v-btn>
            <v-btn
              dark
              flat
              color="pink"
              @click="$router.back()"
            >
              Batal
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import evbus from "@/evbus";
import { User__update, User__findOne } from "@/services/users";
import { defUser } from "@/models/constants";

export default {
  name: 'AddUser',
  props: ['id'],
  data: () => ({
    user: defUser()
  }),
  methods: {
    async updateUser() {
      evbus.$emit("loading-on");
      try {
        const payload = { ...this.user };
        const result = await User__update(this.id, payload);
        evbus.$emit("success", "Sukses mengubah user");
        this.$router.back();
      } catch (err) {
        evbus.$emit("error", "Gagal mengubah user");
        console.log(err);
      } finally {
        evbus.$emit("loading-off");
      }
    },
    async findUser() {
      evbus.$emit("loading-on");
      try {
        let user = await User__findOne(this.id);
        let { _id, ...data } = user;
        this.user = data;
        evbus.$emit("success", "Sukses memuat data user");
      } catch (err) {
        evbus.$emit("error", "Gagal memuat data user");
        console.log(err);
      } finally {
        evbus.$emit("loading-off");
      }
    }
  },
  mounted() {
    this.findUser();
  }
}
</script>

